.TodoListItem{
    padding: 1rem;
    display: flex;
    align-items: center;
    &:nth-child(even){
        background-color: rgb(236, 236, 236);
    }
    .Checkbox{
        cursor: pointer;
        flex: 1;
        display: flex;
        align-items: center;
        svg{
            font-size: 1.5rem;
        }
        .text{
            margin-left: 0.5rem;
            flex: 1;
        }
        &.checked{
            svg{
                color: #1d7481;
            }
            .text{
                color: #adb5bd;
                text-decoration: line-through;
            }
        }
    }
    .remove{
        display: flex;
        align-items: center;
        font-size: 1.5rem;
        color: #ff6d6d;
        cursor: pointer;
        &:hover{
            color:#ff6d6d;
        }
    }
    & + &{border-top: 1px solid #adb9c4;}
}