.TodoInsert{
    display: flex;
    background-color: #333;
    input{
        background: none;
        outline: none;
        border: none;
        padding: 0.5rem;
        font-size: 1.125rem;
        color: #fff;
        &::placeholder{
            color: #eee;
        }
        flex: 1;
    }
    button{
        background: none;
        outline: none;
        border: none;
        background-color: rgb(156, 156, 156);      
        color: #fff;
        padding-left: 1rem;
        padding-right: 1rem;
        font-size: 1.5rem;
        display: flex;
        align-items: center;
        cursor: pointer;
        transition: background 0.1s ease-in 0s;
        &:hover{
            background-color: rgb(211, 211, 211);
        }  
    }
}